	export default{
		data(){
			return{
				isPassword:true,
				isPassword1:true,
				email:'',
				email1:'',
				code:'',
				codename: ' 获取验证码',
				password:'',
				password1:'',
				shade:false,
				disabled:false,
				
			}
		}, 
		onLoad() {
			var _this=this;
			uni.request({
				url:this.url+'delemail/',
				method:'GET',
				header:{
					Authorization:'JWT'+' '+this.global_.token
				},
				success(res) {
					var email=res.data.data;
					var email1=res.data.data;
					_this.email1=email1;
					var l = email.split('@');
					email = l[0].substr(0, l[0].length - 5) + '****@' + l[1]
					_this.email=email
				}
			})
		},
        onBackPress(option){
              plus.key.hideSoftKeybord()
            },
		methods:{
			show:function(){
				this.isPassword=!this.isPassword
			},
			show1:function(){
				this.isPassword1=!this.isPassword1
			},
			getCodeValue: function(e) {
				this.code = e.detail.value;
			},
			getPassword:function(e){
				this.password=e.detail.value
			},
			getPassword1:function(e){
				this.password1=e.detail.value
			},
			getCode: function () {
			      var _this = this;
			        uni.request({
			          method: 'POST',
			          data: {
			            email: this.email1,
			          },
			            //短信接口
			          url:this.url+'setemail/',
			          header: {
			             Authorization:'JWT'+' '+this.global_.token
			          },
			          success(res) {
			            //根据code判断
			            var num = 121;
			            var timer = setInterval(function () {
			              num--;
			              if (num <= 0) {
			                clearInterval(timer);
							
			                _this.codename = '重新发送',
			                _this.disabled = false
			              } else {
							
			                _this.codename = num + "s"
			                _this.disabled = true
			              }
			            }, 1000)
			          }
			        })
			},
			//获取验证码
			getCodeNumber: function(e) {
				this.getCode();
				var _this = this;
			},
			setPwd:function(){
				if(this.code==''){
					uni.showToast({
						icon:'none',
						title:'请输入验证码',
						duration:2000
					})
					return false;
				}
				if(this.password==''){
					uni.showToast({
						icon:'none',
						title:'请输入交易密码',
						duration:2000
					})
					return false;
				}
				var f=this.global_.checkPassword(this.password)
				if(!f){
					uni.showToast({
						title:'交易密码为六位数字!',
						icon:'none',
						duration:2000
					})
					return false
				}
				if(this.password1==''){
					uni.showToast({
						icon:'none',
						title:'请确认交易密码',
						duration:2000
					})
					return false;
				}
				if(this.password1!==this.password){
					uni.showToast({
						icon:'none',
						title:'两次密码不一致',
						duration:2000
					})
					return false;
				}
				uni.request({
					url:this.url+'setmoney/', 
					method:'POST',
					data:{
						email:this.email1,
						email_msg:this.code,
						password:this.password,
						password1:this.password1
						
					},
					header:{
						Authorization:'JWT'+' '+this.global_.token
					},
					success(res) {
						if(res.statusCode==200){
							uni.showToast({
								title:'资金密码已设置',
								icon:'none',
								duration:2000
							})
							uni.switchTab({
								url:'../../pages/my/my'
							})
						}
						if(res.statusCode==400){
							uni.showToast({
								title:'验证码错误',
								icon:'none',
								duration:2000
							})
						}
					}
				})
			}
		}
	}
